@import url(https://fonts.googleapis.com/css?family=Lobster|Lora:400,400i,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* :root {
  --main-bg-color: #f99192;
  --main-txt-color: #f99192;
} */

#main {
  display: flex;
  flex-direction: column;
  height: 100vh;
  text-align: center;
  background-color: var(--main-bg-color);
  font-family: "Lora", serif;
  font-size: 18px;
  color: var(--main-txt-color);
  padding-bottom: 50px;
}

#title {
  padding: 20px;
  color: #fff;
  font-family: "Lobster", cursive;
  font-weight: 400;
}

#quote-box {
  margin: 0 auto;
  width: 80%;
  background-color: #fff;
  border-radius: 5px;
  height: 70vh;
  position: relative;
}

#text {
  text-align: center;
  padding: 2% 20%;
  font-style: italic;
  height: 30vh;
}

#author {
  text-align: right;
  padding: 2% 5%;
  font-size: 14px;
}

#new-quote,
#tweet-quote {
  width: 80px;
  padding: 5px;
  border: none;
  border-radius: 5px;
  background-color: var(--main-bg-color);
  color: #fff;
  margin-bottom: 20px;
  font-size: 12px;
}

#tweet-quote {
  border-radius: 5px;
  margin-right: 20px;
}

#new-quote:focus,
#tweet-quote:focus {
  outline: none;
}

#new-quote:hover,
#tweet-quote:hover {
  background-color: #fff;
  color: var(--main-txt-color);
  border: 1px solid var(--main-txt-color);
}

@media screen and (min-width: 768px) {
  #main {
    font-size: 25px;
  }

  #quote-box {
    margin: 0 auto;
    width: 50%;
  }

  #title {
    padding: 50px;
  }

  #text {
    padding: 2% 10%;
    height: 20vh;
  }

  #author {
    padding: 2% 10%;
    font-size: 16px;
  }

  #quote-box {
    height: 50vh;
  }

  #new-quote,
  #tweet-quote {
    width: 120px;
    padding: 15px;
    font-weight: 700;
    font-size: 14px;
  }
}

